@import "../../css-config/mixins.scss";

.main-header{
    background:#212121;
    color: #fff;
    padding: 0 20px;
    border-bottom: 1px solid rgba(0,0,0,0.1);
    z-index: 99;
    width: 100%;

    .header-content{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        max-width: 1200px;
        margin: 0 auto;
        @include md{
            height: 80px;
        }

        .left{
            display: none;
            gap:25px;
            list-style-type: none;
            @include md{
                display: flex;
            }

            li{
                font-size: 14px;
                font-weight: 600;
                text-transform: uppercase;
                cursor: pointer;
            }
        }

        .center{
            font-size: 22px;
            font-weight: 700;
            cursor: pointer;
            letter-spacing: 2px;
            @include md{
                font-size: 34px;
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
            }
        }

        .right{
            display: flex;
            align-items: center;
            gap: 20px;
            @include md{
                gap: 25px;
            }

            svg{
                font-size: 20px;
                cursor: pointer;
                @include md{
                    font-size: 24px;
                }
            }

            .cart-icon{
                position: relative;
                span{
                    min-width: 20px;
                    background: #8e2de2;
                    position: absolute;
                    top: -5px;
                    right: -12px;
                    border-radius: 10px;
                    padding: 2.5px;
                    font-size: 12px;
                    text-align: center;
                    line-height: 1;
                }
            }
        }
    }

    &.sticky-header{
        position: sticky;
        top: 0;
        transform: translateY(-80px);
        animation: stickyHeader 0.3s ease forwards;
    }
}

@keyframes stickyHeader {
    0%{
        transform: translateY(-80px);
    }
    100%{
        transform: translateY(0);
    }
}